import {Component, OnInit, ViewChild} from '@angular/core';
import {PageBase} from 'app/shared/classes/page-base.class';
import {HttpService} from 'app/shared/services/http.service';
import {ServerMatch} from 'app/shared/servermatch';
import {MatTableDataSource, MatPaginator} from '@angular/material';
import {MessageService} from 'app/shared/services/message.service';
import {MsgType} from 'app/shared/enum/msgtype.enum';
import {WindowService} from 'app/shared/services/window.service';

@Component({
  selector: 'app-timelapse',
  templateUrl: './timelapse.component.html',
  styleUrls: ['./timelapse.component.scss']
})
export class TimelapseComponent extends PageBase implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;

  usbImage;
  departures = [];
  selectedDeparture = '';

  displayedColumns = ['name', 'delete']; // maybe add 'size' and 'image_num' ?
  dataSource = new MatTableDataSource();
  selectedTimelapse = '';

  constructor(private hs: HttpService, private ms: MessageService,
    public ws: WindowService) {
    super('Timelapse');
    this.usbImage = ServerMatch.STATIC + 'assets/usb.png';
  }

  ngOnInit() {
    this.initTable();
    this.getDepartures();
    this.getTimelapseList();
  }

  initTable() {
    setTimeout(() => {
      this.dataSource.data = [];
      this.paginator._intl.itemsPerPageLabel = "";
      this.dataSource.paginator = this.paginator;
    });
  }

  getDepartures() {
    this.hs.post('directory', {cd: ''}).subscribe(res => {
      if (res.data.length === 0 || (res.data.length === 1 && res.data[0] === '')) {
        this.departures = [];
      } else {
        this.departures = res.data;
      }
    });
  }

  changeDeparture(usb) {
    this.selectedDeparture = usb;
  }

  getTimelapseList() {
    this.hs.options('timelapse').subscribe(res => {
      this.dataSource.data = this.makeDataTable(res.list);
    });
  }

  exportTimelapse() {
    this.hs.post('timelapse', {
      dirname: this.selectedTimelapse,
      usbname: this.selectedDeparture,
    }, {
        spin: true,
        spinTimeout: 60,
        throwError: true,
      }).subscribe(res => {
        this.ms.open(MsgType.info);
        this.clearOut();
      }, err => {
        this.ms.open(MsgType.error);
      });
  }

  deleteTimelapse($event, item) {
    $event.stopPropagation();
    this.hs.put('timelapse', {dirname: item.name}).subscribe(res => {
      this.ms.open(MsgType.info);
      this.getTimelapseList();
    });
  }

  private makeDataTable(data) {
    const res = []
    data.forEach(el => {
      res.push({
        'name': el,
      });
    });

    return res;
  }

  clearOut() {
    this.selectedDeparture = '';
    this.selectedTimelapse = '';
  }

  selectTimelapse(item) {
    this.selectedTimelapse = item.name;
  }

  isSelected(row) {
    return this.selectedTimelapse === row.name;
  }

  shouldPrintButtonBeActive() {
    return (this.selectedDeparture) && (this.selectedTimelapse);
  }
}



