import {Component, OnInit, Inject} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {HttpService} from 'app/shared/services/http.service';
import {MessageService} from 'app/shared/services/message.service';
import {MsgType} from 'app/shared/enum/msgtype.enum';
import {Axis} from 'app/shared/classes/axis.interface';
import {DataService} from 'app/shared/services/data.service';
import {TranslatorService} from 'app/shared/services/translator.service';

@Component({
  selector: 'app-move',
  templateUrl: './move.component.html',
  styleUrls: ['./move.component.scss']
})
export class MoveComponent implements OnInit {
  allowMove = false;
  moveValue = 20;
  pos: Axis = {X: 0, Y: 0, Z: 0};
  ext_num = 1;
  extruders_number = 1;
  accuracy = 1;

  constructor(public dialogRef: MatDialogRef<MoveComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
    private hs: HttpService, private ms: MessageService, private ds: DataService,
    public trl: TranslatorService) {
  }

  ngOnInit() {
    this.checkHome();
    this.checkExtrudersNumber();
  }

  checkExtrudersNumber() {
    this.ds.printerSettings$.subscribe(res => {
      this.extruders_number = res.extruders;
    });
  }

  move(axis, isPos) {
    // TODO: check for extruder number
    const value = isPos ? this.accuracy : -this.accuracy;
    this.hs.post('move_axis', {axis, value}).subscribe(res => {
      this.pos[axis] += value;
    });
  }

  checkHome() {
    this.hs.options('move_axis').subscribe(res => {
      if (res['access'])
        this.allowMove = true;
      else
        this.allowMove = false;
    });
  }

  home(axis) { // 'X' | 'Y' | 'Z' | 'All'
    this.hs.post('home', {axis}).subscribe(res => {
      this.ms.open(MsgType.info);
      this.checkHome();
    });
  }

  handWheel(isSet) {
    // TODO: not needed yet
  }

}



