import {Component, OnInit} from '@angular/core';
import {PageBase} from 'app/shared/classes/page-base.class';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {HttpService} from 'app/shared/services/http.service';
import {PrinterSettings} from 'app/shared/classes/printer-settings.interface';
import {TranslatorService} from 'app/shared/services/translator.service';
import {NEEDS_REFRESH, NEEDS_RESTART} from 'app/shared/consts/settings-after-action.const';
import {MatDialog} from '@angular/material';
import {GenericDialogComponent} from 'app/shared/components/generic-dialog/generic-dialog.component';
import {DialogType} from 'app/shared/enum/dialog.enum';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent extends PageBase implements OnInit {
  settingsForm: FormGroup;
  settingsLastSaved: PrinterSettings;
  anyChanges = false;

  constructor(private hs: HttpService, public trl: TranslatorService,
    private dialog: MatDialog) {
    super("Settings");
  }

  ngOnInit() {
    this.initForm();
    this.getInitialSettingsInfo();
    this.settingsForm.valueChanges.subscribe(fields => this.fieldChanged(fields));
  }

  getInitialSettingsInfo() {
    this.hs.get('settings').subscribe((res: PrinterSettings) => {
      this.settingsLastSaved = res;
      this.anyChanges = false;
      Object.keys(res).forEach(el => {
        this.settingsForm.controls[el].setValue(res[el]);
      });
    });
  }

  initForm() {
    this.settingsForm = new FormBuilder().group({
      view3d: [false],
      rotate: [{value: false}],
      abs: [false],
      timelapse: [false],
      filament: [false],
      lcd: [false],
      width: [0, [Validators.required]],
      height: [0, [Validators.required]],
      extruders: [1, [Validators.required]],
      serial_port: ['/dev/ttyUSB0', [Validators.required]],
      baudrate: [250000, [Validators.required]],
      printing_buffer: [15, [Validators.required]],
      bedleveling_X1: [50, [Validators.required]],
      bedleveling_X2: [180, [Validators.required]],
      bedleveling_Y1: [50, [Validators.required]],
      bedleveling_Y2: [180, [Validators.required]],
      traveling_feedrate: [3000, [Validators.required]],
      bedleveling_Z_offset: [10, [Validators.required]],
      bedleveling_Z_move_feedrate: [1500, [Validators.required]],
      hibernate_Z_offset: [5, [Validators.required]],
      hibernate_Z_move_feedrate: [1500, [Validators.required]],
      pause_Z_offset: [10, [Validators.required]],
      pause_Z_move_feedrate: [1500, [Validators.required]],
      X_pause_position: [0, [Validators.required]],
      Y_pause_position: [0, [Validators.required]],
      X_timelapse_position: [0, [Validators.required]],
      Y_timelapse_position: [0, [Validators.required]],
    });
  }

  fieldChanged(fields = null) {
    if (!fields) {
      fields = Object.keys(this.settingsForm.controls).reduce((a, b) => {
        a[b] = this.settingsForm.controls[b].value;
        return a;
      }, {});
    }
    if (!this.settingsLastSaved)
      return;
    this.anyChanges = Object
      .keys(fields)
      .some(el => fields[el] !== this.settingsLastSaved[el]);
  }

  submit() {
    const changedFields = {};
    Object.keys(this.settingsLastSaved).forEach(el => {
      const originalValue = this.settingsLastSaved[el];
      const formValue = this.settingsForm.controls[el].value;
      if (originalValue !== formValue) {
        changedFields[el] = formValue;
      }
    });
    this.hs.post('settings', changedFields).subscribe(data => {
      let needsRefresh = false, needsRestart = false;
      Object.keys(changedFields).forEach(el => {
        // update new values
        this.settingsLastSaved[el] = changedFields[el];

        // check the need for refresh/restart
        needsRefresh = needsRefresh || NEEDS_REFRESH.some(ref => ref === el);
        needsRestart = needsRestart || NEEDS_RESTART.some(res => res === el);
      });
      this.fieldChanged();

      // notify user if refresh/restart needed
      if (!needsRefresh && !needsRestart)
        return;
      this.dialog.open(GenericDialogComponent, {
        data: {
          usage: DialogType.afterSettings,
          needsRefresh, needsRestart
        }
      });
    });
  }
}



