import {Component, OnInit} from '@angular/core';
import {HttpService} from 'app/shared/services/http.service';
import {MessageService} from 'app/shared/services/message.service';
import {MsgType} from 'app/shared/enum/msgtype.enum';
import {PageBase} from 'app/shared/classes/page-base.class';

@Component({
  selector: 'app-bedlevel',
  templateUrl: './bedlevel.component.html',
  styleUrls: ['./bedlevel.component.scss']
})
export class BedlevelComponent extends PageBase implements OnInit {

  constructor(private hs: HttpService, private ms: MessageService) {
    super('Bedleveling');
  }

  ngOnInit() {
  }

  bedLevelOnStage(stage) {
    this.hs.post('bedleveling', {stage}).subscribe(res => {
      this.ms.open(MsgType.info);
    });
  }

}



