import {TranslatorService} from "../services/translator.service";

const SETTINGS_TOOLTIPS = {
  view3d: 'Whether to show 3D model of the selected gcode or not. Assume high CPU consumption',
};

export class Tooltip {
  /**
   * @param trl provide a Translator Service class to this
   * then make an instance in your component and use that
   * anywhere in HTML with these given functionalities!
   */
  constructor(private trl: TranslatorService) {
  }

  get(key) {
    return SETTINGS_TOOLTIPS.hasOwnProperty(key) && this.trl.lookup(SETTINGS_TOOLTIPS[key]) || '';
  }

  class() {
    return 'font-' + this.trl.lang$.getValue();
  }

  pos() {
    return 'above';
  }

  delay() {
    return 20;
  }
}



