import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class SpinnerService {
  // all the "en"ed spinners must be "dis"ed
  // in order to disable spinner block ui
  // if not, it will be disabled automatically
  // after the default timeout seconds' passed
  spin$ = new BehaviorSubject<boolean>(false);
  count = 0;
  timeoutCount = 0;
  defaultTimeout = 5;

  constructor() {
  }

  // activates spinner (with timeout (seconds))
  en(timeoutSeconds = this.defaultTimeout) {
    this.count++;
    this.timeoutCount++;

    this.spin$.next(true);

    setTimeout(() => this.checkError(), timeoutSeconds * 1000);
  }

  // deactivates spinner
  dis() {
    this.count--;
    if (this.count <= 0) {
      this.spin$.next(false);
      this.count = 0;
    }
  }

  // check error after timeout
  checkError() {
    this.timeoutCount--;
    console.debug(`(Count: ${this.count}, TimeoutCount: ${this.timeoutCount})`);
    if (this.timeoutCount > 0)
      return;

    if (this.count <= 0)
      return;

    console.error("spinner timeout is passed. disabling...");
    this.spin$.next(false);
    this.timeoutCount = 0;
  }
}



