import {Component, OnInit} from '@angular/core';
import {PageBase} from 'app/shared/classes/page-base.class';
import {DataService} from 'app/shared/services/data.service';
import {HttpService} from 'app/shared/services/http.service';
import {MatDialog} from '@angular/material';
import {GenericDialogComponent} from 'app/shared/components/generic-dialog/generic-dialog.component';
import {DialogType} from 'app/shared/enum/dialog.enum';
import {Router} from '@angular/router';
import {MessageService} from 'app/shared/services/message.service';
import {MsgType} from 'app/shared/enum/msgtype.enum';
import {ServerMatch} from 'app/shared/servermatch';

@Component({
  selector: 'app-extruder',
  templateUrl: './extruder.component.html',
  styleUrls: ['./extruder.component.scss']
})
export class ExtruderComponent extends PageBase implements OnInit {
  value = 2;
  feedrate = 500;
  injected = 0;

  upImage;
  downImage;

  constructor(private ds: DataService, private hs: HttpService,
    private dialog: MatDialog, private router: Router,
    private ms: MessageService) {
    super('Extrude');

    this.upImage = ServerMatch.STATIC + 'assets/up.png';
    this.downImage = ServerMatch.STATIC + 'assets/down.png';
  }

  ngOnInit() {
    this.checkInitialTemperature();
  }

  checkInitialTemperature() {
    setTimeout(() => {
      if (this.ds.temps$.getValue().ext['cur'] < 178) {
        this.dialog.open(GenericDialogComponent, {
          data: {
            usage: DialogType.needPreheating,
          }
        }).afterClosed().subscribe(res => {
          this.router.navigate(['/home']);
        });
      }
    });
  }

  extrude(out = false) {
    const extrudeValue = out ? this.value : -this.value;
    this.hs.post('extrude', {
      value: extrudeValue,
      feedrate: this.feedrate
    }).subscribe(res => {
      this.injected += extrudeValue;
      this.ms.open(MsgType.info);
    });
  }

}



